body {
  text-align: center;
  background-color: teal;
}

p {
  text-align: left;
}

.form {
  width: 210px;
  background-color: #d2691e;
  border: 2px solid tomato;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

label {
  width: 200px;
  height: 50px;
  cursor: pointer;
  border: none;
  font-size: 20px;
}

input {
  width: 200px;
  height: 25px;
  cursor: pointer;
  text-align: center;
  border: none;
  font-size: 17px;
}

button {
  width: 200px;
  height: 50px;
  cursor: pointer;
  background-color: gray;
  border: none;
  font-size: 20px;
}

button:hover, button:focus {
  background-color: #adff2f;
  border: 2px solid #ccc;
}

